/* src/app/home/home.scss */
.home-content {
  max-width: 760px;
  margin: 0 auto;
  padding: 36px 24px;
  background:
    linear-gradient(
      180deg,
      rgba(52, 152, 219, 0.03),
      rgba(46, 204, 113, 0.02));
  border-radius: 12px;
  box-shadow: 0 8px 30px rgba(18, 52, 76, 0.06);
}
.home-content h2 {
  color: #0f3b57;
  font-size: 30px;
  margin-bottom: 18px;
  text-align: left;
  font-weight: 700;
  background:
    linear-gradient(
      90deg,
      #2bb6ff,
      #28e0a4);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}
.home-content p {
  color: #2b4858;
  text-align: left;
  font-size: 14px;
  margin-bottom: 18px;
}
.task-input-section {
  display: flex;
  gap: 12px;
  margin-bottom: 20px;
}
.task-input-section input {
  flex: 1;
  padding: 12px 16px;
  border: 1px solid rgba(15, 59, 87, 0.08);
  border-radius: 10px;
  font-size: 14px;
  transition: all 0.22s ease;
  background: rgba(255, 255, 255, 0.9);
}
.task-input-section input:focus {
  outline: none;
  box-shadow: 0 6px 18px rgba(43, 126, 166, 0.08);
  border-color: rgba(40, 224, 164, 0.45);
}
.task-input-section input::placeholder {
  color: #9fb6bf;
}
.task-input-section button {
  padding: 10px 20px;
  background:
    linear-gradient(
      135deg,
      #23a6d5,
      #2ee6a5);
  color: white;
  border: none;
  border-radius: 10px;
  font-weight: 700;
  cursor: pointer;
  transition: transform 0.18s ease, box-shadow 0.18s ease;
  box-shadow: 0 6px 18px rgba(35, 166, 213, 0.18);
}
.task-input-section button:hover {
  transform: translateY(-3px);
}
.task-input-section button:active {
  transform: translateY(0);
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
ul li {
  background:
    linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.98),
      rgba(245, 255, 250, 0.98));
  padding: 16px 20px;
  margin-bottom: 14px;
  border-radius: 12px;
  border-left: 6px solid rgba(43, 142, 173, 0.12);
  box-shadow: 0 8px 24px rgba(18, 52, 76, 0.04);
  transition: all 0.25s ease;
  color: #12323f;
  font-size: 15px;
  position: relative;
}
ul li:hover {
  transform: translateY(-3px);
  box-shadow: 0 14px 40px rgba(18, 52, 76, 0.06);
}
ul li.highlighted {
  border-left-color: #2ee6a5;
  background:
    linear-gradient(
      90deg,
      rgba(46, 204, 113, 0.06),
      rgba(43, 142, 173, 0.02));
}
ul li.completed {
  background:
    linear-gradient(
      90deg,
      rgba(220, 230, 235, 0.6),
      rgba(245, 250, 250, 0.6));
  opacity: 0.85;
  border-left-color: #95a5a6;
}
ul li .task-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}
ul li .task-title {
  flex: 1;
  font-weight: 600;
  color: #0f3b57;
}
ul li .task-title.strike {
  text-decoration: line-through;
  color: #7f8c8d;
  font-weight: 500;
}
ul li .task-actions {
  display: flex;
  gap: 8px;
}
ul li button {
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 16px;
  padding: 6px;
  border-radius: 8px;
}
ul li .btn-complete {
  color: #27ae60;
}
ul li .btn-highlight {
  color: #f1c40f;
}
ul li .btn-edit {
  color: #2980b9;
}
ul li .btn-delete {
  color: #e74c3c;
}
